import {mapGetters} from 'vuex'
import CkEditor from '@/components/editor/ck-editor'
import Cities from '@/helpers/city.js'
import Dropzone from 'nuxt-dropzone'
import 'nuxt-dropzone/dropzone.css'
export default {
  components:{CkEditor,Dropzone},
  computed:{
    ...mapGetters({
      'getCategories':'category/getCategories',
      'getNewAds':'ads/getNewAds'
    })
  },
  data(){
    return{
      MyUploads:[],
       options: {
        url: "http://httpbin.org/anything"
      },
      cities:Cities,
      description:"",
      colors:['red-ads','cyan-ads','blue-ads','orange-ads']
    }
  },
  methods:{
    removeItem(thisFile,i){
        this.$refs.el.removeFile(thisFile)
        this.MyUploads.splice(i,1)
        console.log("File removed!")
    },
    test(e,b){
      this.$console(e)
       this.MyUploads.push(e)

  setTimeout(() => {
   // this.removeItem(this.MyUploads[0])
  }, 3000);
    },
    randomClass(){
     let rnd = Math.floor(Math.random() * this.colors.length);
     return this.colors[rnd];
    },
    setMainCategory(item){
     this.$store.commit('ads/setNewAds',{key:'categories',data:{id:item.id, name:item.name, validations:true}})
     this.$router.push('/give-ads/forms')

    },
    controlMainCategory(){
      if(this.getNewAds.mainCategory.data.length==0){
        this.$router.push('/give-ads')
      }
    },
  },
  mounted(){
      const instance = this.$refs.el
      console.log(instance);

    this.controlMainCategory()
  }
}
